    const { classes, translate } = this.props;
    const { posts } = this.props.doby;
    const { challenge, price } = this.state;

    const summitsCount = challenge ? challenge.summitsRef.length : 0;

    let months = 0;
    let start = "";
    let end = "";
    let difficulty = 0;
    let description = "";
    if (challenge) {
      months = moment(challenge.end).diff(
        moment(challenge.start),
        "months",
        true
      );
      months = Math.abs(months);
      months = Math.ceil(months);
      start = moment(challenge.start).format("DD.MM");
      end = moment(challenge.end).format("DD.MM.YYYY");
      difficulty = challenge.difficulty;
      description = challenge.description;
    }

    const currencySign = getCurrencySymbol(config.currency);
